@import '../utils/mixins';
@import '../utils/variables';

form#create-employee{
  width: 100%;
  @include respond-to($breakpoint-md) {
    width: 50%; 
  }
}

.form-control{
    border-radius: 5px;
    border: 1px solid $color-primary;
}

.custom-btn{
    background-color: $color-tertiary;
    text-align: center;
    color: white;
}
.custom-btn:hover{
    background-color: $color-secondary;
}
.custom-bg{
    background-color: $color-primary;
    border-radius: 5px;
}
.cover{
    transform: translateY(-71px);
    text-align: center;
}

.gradient-background {
   background: $background-image-bottom;
   background-size: cover;
   background-position: center;
   background-color: $color-primary-complement;
}


.display-block{
    display: block !important;
}

.close-modal-btn{
    position: relative;
    right: 0;
    top: -15px;
    left: 95%;
    bottom: 15px;
    width: 24px;
    height: 24px;
    align-items: center;
    justify-content: center;
}